<template>
    <div class="add">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>新增地址</span>
        </header>
        <section class="A1">
            <div><input type="text" placeholder="请填写你的姓名" v-model="name"></input></div>
            <div @click="toAddDetail"><input type="text" placeholder="小区/写字楼/学校等" v-model="address" disabled="true"></input></div>
            <div><input type="text" placeholder="请填写详细送餐地址" v-model="address_detail"></input></div>
            <div><input type="text" placeholder="请填写能够联系到您的手机号" v-model="phone"></input></div>
            <div><input type="text" placeholder="备用联系电话（选填）" v-model="phone_bk"></input></div>
        </section>
        <section class="A2">
            <span @click="addDe">新增地址</span>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Add",
        data(){
            return{
                input:[],
                addressS:[],
                user_id:"",
                address:"",
                address_detail:"",
                geohash:"",
                name:"",
                phone:"",
                tag:"null",
                phone_bk:"",
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            if(localStorage.getItem("address")){
                this.addressS = JSON.parse(localStorage.getItem("address"));
                this.address = this.addressS.name;
                this.geohash = this.addressS.geohash;
            }
            if(localStorage.getItem("input")){
                this.input = JSON.parse(localStorage.getItem("input"))[0];
                this.name = this.input.name;
                this.address_detail = this.input.address_detail;
                this.phone = this.input.phone;
                this.phone_bk = this.input.phone_bk;
                localStorage.removeItem("input");
            }
        },
        methods:{
            addDe(){
                if(this.address!=[]&&this.address_detail!=""&&this.name!=""&&this.phone!="") {
                    this.axios.post("/v1/users/" + this.user_id + "/addresses", {
                        user_id: this.user_id,
                        address: this.address,
                        address_detail: this.address_detail,
                        geohash: this.geohash,
                        name: this.name,
                        phone: this.phone,
                        tag: this.tag,
                        sex: 1,
                        phone_bk: this.phone_bk,
                        tag_type: 4,
                    }).then(res => {
                        // console.log(res);
                        if (res.message != "地址信息错误") {
                            localStorage.removeItem("address");
                            this.$router.push("/address");

                        }
                    }).catch(err => {});
                }
            },
            toAddDetail(){
                this.$router.push({path:'/addDetail',query:{name:this.address}});
                let a = [];
                a.push({name:this.name,address_detail:this.address_detail,phone:this.phone,phone_bk:this.phone_bk});
                localStorage.setItem("input",JSON.stringify(a));
            }
        }
    }
</script>

<style scoped>
    .add{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .A1{
        margin-top: 0.4rem;
        background: #fff;
        padding-top: .4rem;
    }
    .A1 div{
        padding-bottom: .4rem;
    }
    .A1 input{
        display: block;
        width: 90%;
        font-size: .6rem;
        margin: 0 auto;
        padding: .3rem;
        background: #f2f2f2;
        border: 1px solid #ddd;
        border-radius: 3px;
        outline: none;
    }
    .A2{
        margin: .6rem auto;
        width: 15rem;
        background: #4cd964;
        border-radius: 3px;
    }
    .A2 span{
        display: inline-block;
        width: 100%;
        font-size: .6rem;
        color: #fff;
        line-height: 1.6rem;
        text-align: center;
        background: none;
        font-weight: 700;
        opacity: .6;
    }
</style>